import { Component, OnInit } from '@angular/core';
import { LibroclickedService } from '../libroclicked.service';
import { HttpClient, HttpResponse } from '@angular/common/http';

@Component({
  selector: 'app-libros',
  templateUrl: './libros.component.html',
  styleUrls: ['./libros.component.css']
})
export class LibrosComponent implements OnInit {

  libros: any;
  errorHttp: Boolean;
  load: Boolean = true;

  constructor(private http: HttpClient, public Libroclicked: LibroclickedService) {
    /*this.libros = [
      { id: '1', titulo: 'Te ver√© bajo el hielo', autor: 'Robert Bryndza' },
      { id: '2', titulo: 'Dime qui√©n soy', autor: 'Julia Navarro' },
      { id: '3', titulo: 'El d√≠a que se perdi√≥ la cordura', autor: 'Javier Castillo' }]*/
  }

  ngOnInit(): void {
    this.cargarLista();
  }

  showAutor(_libro: any): void {
    alert("Escrito por " + _libro.autor);
  }

  cargarLista() {
    this.load = true;
    this.http.get('assets/lista-libros.json').subscribe(
      (respuesta: Response) => { this.libros = respuesta; this.load = false },
      (respuesta: Response) => { this.errorHttp = true },
    );
  }

  agregarLibro(_libroListo) {
    this.Libroclicked.libroVisto(_libroListo);
  }
}
