import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';//Manejar parametros y ruta activa

@Component({
  selector: 'app-informacion',
  templateUrl: './informacion.component.html',
  styleUrls: ['./informacion.component.css']
})
export class InformacionComponent implements OnInit {
  libroId: number;
  libroClick: any;
  libros: any;

  constructor(private rutausuario: ActivatedRoute) {
    this.libros = [
      { id: '1', titulo: 'Te ver√© bajo el hielo', autor: 'Robert Bryndza' },
      { id: '2', titulo: 'Dime qui√©n soy', autor: 'Julia Navarro' },
      { id: '3', titulo: 'El d√≠a que se perdi√≥ la cordura', autor: 'Javier Castillo' }]
  }


  ngOnInit(): void {
    this.rutausuario.params.subscribe(params => {
      this.libroId = params['libroId'];
      this.libroClick = this.libroBuscador();
    })
  }

  libroBuscador() {
    return this.libros.filter(this.filtroId, this.libroId)[0];
  }

  filtroId(libro) {
    return libro.id == this;
  }

}
