import { Component, OnInit, Renderer2 } from '@angular/core';

@Component({
  selector: 'app-renderer',
  templateUrl: './renderer.component.html',
  styleUrls: ['./renderer.component.css']
})
export class RendererComponent implements OnInit {
  alumnos: Array<object>
  clearElement: HTMLElement;

  constructor(private renderer: Renderer2) {
    this.alumnos = [
      { nombre: "David", id: 1 },
      { nombre: "Daniel", id: 2 },
      { nombre: "Ana", id: 3 },
      { nombre: "Lorena", id: 4 },
      { nombre: "Jose", id: 5 },
    ]
  }

  ngOnInit(): void {
  }

  activeMethod(element: HTMLElement) {
    if (this.clearElement) {
      this.renderer.removeClass(this.clearElement, "miclase");
    }
    this.renderer.addClass(element, "miclase");
    this.renderer.setAttribute(element, "data-select", "true");
    this.clearElement = element;

    let nuevoElemento = this.renderer.createElement("span");

    this.renderer.setProperty(nuevoElemento,"innerHTML","‚óçüí™");
    this.renderer.appendChild(element, nuevoElemento);


  

  }

}
