import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { Prueba01Component } from './prueba01/prueba01.component';
import { RandomComponent } from './random/random.component';
import { ColoresComponent } from './colores/colores.component';
import { FirmaComponent } from './firma/firma.component';
import { LibrosComponent } from './libros/libros.component';
import { SobreNosotrosComponent } from './sobre-nosotros/sobre-nosotros.component';
import { CabeceraComponent } from './cabecera/cabecera.component';
import { InicioComponent } from './inicio/inicio.component';
import {RouterModule, Routes} from '@angular/router';
import { InformacionComponent } from './informacion/informacion.component';
import { HeaderComponent } from './cabecera/header/header.component';
import { FooterComponent } from './cabecera/footer/footer.component';
import { NavigationComponent } from './cabecera/navigation/navigation.component';

import {LibroclickedService} from './libroclicked.service';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { MatSliderModule } from '@angular/material/slider';
import { RendererComponent } from './renderer/renderer.component';

const rutas:Routes = [
  {path:'informacion/:libroId', component: InformacionComponent},//Asi pasamos un parametro con :idLibro
  {path:'informacion/', redirectTo: '/'},//Asi controlamos si no se pone idLibro
  {path:'listado-libros', component: LibrosComponent},
  {path:'', component: InicioComponent, pathMatch: 'full'},//ruta por defecto
  {path:'**', redirectTo: '/'}
]

@NgModule({
  declarations: [
    AppComponent,
    Prueba01Component,
    RandomComponent,
    ColoresComponent,
    FirmaComponent,
    LibrosComponent,
    SobreNosotrosComponent,
    CabeceraComponent,
    InicioComponent,
    InformacionComponent,
    HeaderComponent,
    FooterComponent,
    NavigationComponent,
    RendererComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot(rutas),
    BrowserAnimationsModule,
    MatSliderModule
  ],
  providers: [
    LibroclickedService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
